<template>
  <view class="wrap">
    <view class="spinner"></view>
    <text class="tip">正在处理登录，请稍候…</text>
  </view>
</template>

<script>
export default {
  onLoad(query) {
    const { code, state } = query;
    if (!code) {
      uni.showModal({
        content: '缺少微信授权 code',
        showCancel: false,
        complete: () => uni.navigateBack()
      });
      return;
    }
    this.loginByCode(code, state);
  },

  methods: {
    /* 用 code 换取业务 token */
    loginByCode(code, state) {
      uni.request({
        url: '/api/loginByWechatCode',
        method: 'POST',
        data: { code, state },
        success: ({ data }) => {
          if (data.success) {
            uni.setStorageSync('token', data.token);
            uni.showToast({ title: '登录成功', icon: 'success' });
            // 返回首页（或上一页）
            uni.switchTab({ url: '/pages/index/index' });
          } else {
            this.showError(data.message || '登录失败');
          }
        },
        fail: () => this.showError('网络错误')
      });
    },

    showError(msg) {
      uni.showModal({
        content: msg,
        showCancel: false,
        complete: () => uni.navigateBack()
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #fff;
}

.spinner {
  width: 40px;
  height: 40px;
  border: 3px solid #f3f3f3;
  border-top-color: #07c160;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 15px;
}

.tip {
  font-size: 16px;
  color: #333;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
</style>